import React from 'react';

import s from './Post.module.css';

const Post = (props) => {
  return (
    <div>
      <div className={s.item}>
        <img src='data:image/png;base64,' />
        { props.message }
        <div>
          <span>like</span> { props.likesCount }
        </div>
      </div>
    </div>
  )
}

export default Post;